import './App.css';
import React, { useState } from 'react';

function App() {
  const [userInput, setUserInput] = useState([]);
  const [result, setResult] = useState('');

  const buttonArray = [
    { id: "equals", sign: "=" },
    { id: "zero", sign: "0" },
    { id: "one", sign: "1" },
    { id: "two", sign: "2" },
    { id: "three", sign: "3" },
    { id: "four", sign: "4" },
    { id: "five", sign: "5" },
    { id: "six", sign: "6" },
    { id: "seven", sign: "7" },
    { id: "eight", sign: "8" },
    { id: "nine", sign: "9" },
    { id: "add", sign: "+" },
    { id: "subtract", sign: "-" },
    { id: "multiply", sign: "*" },
    { id: "divide", sign: "/" },
    { id: "decimal", sign: "." },
    { id: "clear", sign: "C" },
  ];

  const handleKeyDown = (sign) => {
    if (sign === "C") {
      setUserInput([]);
      return;
    }
  
    if (sign === "=") {
      calculateResult();
      return;
    }
  
    const lastInput = userInput[userInput.length - 1];
    const operators = ['+', '-', '*', '/'];
  
    if (operators.includes(sign)) {
      // If the last input was an operator, replace it with the new one
      if (operators.includes(lastInput)) {
        setUserInput([...userInput.slice(0, -1), sign]);
      } else {
        setUserInput([...userInput, sign]);
      }
      return;
    }
  
    const lastNumberSegment = userInput.join("").split(/[\+\-\*\/]/).pop();
  
    if (sign === "0") {
      if (lastNumberSegment === "0" && lastInput !== ".") {
        return;
      }
    }
  
    if (sign === ".") {
      if (lastNumberSegment.includes(".")) {
        return;
      }
    }
  
    setUserInput([...userInput, sign]);
  };
  

  const calculateResult = () => {
    try {
      const expression = userInput.join("");
      const calculatedResult = eval(expression);
      setResult([calculatedResult.toString()]);
      setUserInput([]);
    } catch (error) {
      setUserInput(["Error"]);
    }
  };

  return (
    <div className="App">
      <div id="calculator">
        {buttonArray.map((obj) => (
          <button key={obj.id} id={obj.id} onClick={() => handleKeyDown(obj.sign)}>
            {obj.sign}
          </button>
        ))}
      </div>
      <div id="display">{userInput.join("")}{result}</div>
    </div>
  );
}

export default App;
